import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class QuestionsService {

  constructor() { }

	public getQuestions(url): Promise<Array<any>> {
		return new Promise(async (resolve) => {
			const response = await fetch(url);
			
			const json = await response.json();

			resolve(json);
		})
	}
}
